import {
	StyledTileLarge,
	StyledTileSmall,
	Image,
	Content,
	Title,
	Year,
	Tags,
	Tag,
} from "./styled";
import posterLarge from "../images/posterLarge.png";
import posterSmall from "../images/posterSmall.png";
import { Rating } from "../Rating";

export const TileLarge = ({ ratingValue, voteAmount, title, year, tag }) => {
	return (
		<StyledTileLarge>
			<Image src={posterLarge} alt="" />
			<Content>
				<Title>{title}</Title>
				<Year>{year}</Year>
				<Tags>
					<Tag>{tag}</Tag>
					<Tag>{tag}</Tag>
					<Tag>{tag}</Tag>
				</Tags>
			</Content>
			<Rating // to jest Rating dla kafelkw na MovieList
				ratingValue={ratingValue}
				voteAmount={voteAmount}
			/>
			{/* <Rating // ten Rating ma by w kafelku na tle zdjcia filmu w MovieDetails
                isOnMoviePhoto
                ratingValue={ratingValue}
                voteAmount={voteAmount}
            />
            <Rating // ten Rating ma by w kafelku z danymi filmu w MovieDetails
                isMovieDetails
                ratingValue={ratingValue}
                voteAmount={voteAmount}
            /> */}
		</StyledTileLarge>
	);
};

export const TileSmall = ({ ratingValue, voteAmount, title, year, tag }) => (
	<StyledTileSmall>
		<Image src={posterSmall} alt="" />
		<Content>
			<Title>{title}</Title>
			<Year>{year}</Year>
			<Tags>
				<Tag>{tag}</Tag>
				<Tag>{tag}</Tag>
				<Tag>{tag}</Tag>
			</Tags>

			<Rating // to jest Rating dla kafelkw na MovieList
				ratingValue={ratingValue}
				voteAmount={voteAmount}
			/>
		</Content>
	</StyledTileSmall>
);
